@import url('https://fonts.googleapis.com/css2?family=Anta&display=swap');

.slider {
  width: 100%;
  max-height: 800px;
  height: 100%;
  padding: 30px 15px;
  margin: 30px 0;
  border: 6px solid black;
  border-radius: 30px;
  backdrop-filter: sepia(90%);
  .slider-img {
    max-height: 500px;
    height: 100%;
    max-width: 400px;
    width: 100%;
    border: 1px solid black;
    object-fit: cover;
  }
}
.slide-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-height: 650px;
  height: 100%;
}
.slider-slide {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  max-height: 650px;
  height: 100%;
  max-width: 400px;
  width: 100%;
}
.slider-slide-title {
  text-align: center;
  padding-top: 10px;
  font-size: calc(10px + 6 *(100vw / 1440));
  font-family: "Anta", sans-serif;
  font-weight: 400;
  font-size: 20px;
  font-style: normal;
}
.swiper-wrapper {
  display: flex;
  align-items: center;
}