@import url('https://fonts.googleapis.com/css2?family=Anta&display=swap');

.container-wrapper {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 30px 15px;
}
.comic {
    border-radius: 10px;
    overflow: hidden;
    background-color: rgb(176, 176, 176);
    height: 680px;
    position: relative;
}
.wishlist-title {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 0;
  h1 {
    color: #fff;
    background-color: red;
    padding: 30px 120px;
    font-family: "Anta", sans-serif;
    font-weight: 400;
    font-size: 60px;
    font-style: normal;
  }
}